import { Model } from '../base/Model';
export class BasketModel extends Model {
    constructor(data, events) {
        super(data, events);
        this.resetState();
    }
    set count(value) {
        this._count = value;
    }
    get count() {
        return this._count;
    }
    set items(value) {
        this._items = Array.isArray(value) ? value : [value];
    }
    get items() {
        return this._items;
    }
    set total(value) {
        this._total = value;
    }
    get total() {
        return this._total;
    }
    addCardToBasket(card) {
        card.isInBasket = true;
        this.items.push(card.id);
        if (typeof card.price === 'number') {
            this.total += card.price;
        }
        this.count += 1;
        this.events.emit('basket:change');
    }
    removeCardFromBasket(card) {
        card.isInBasket = false;
        if (typeof card.price === 'number') {
            this.total -= card.price;
        }
        this.count -= 1;
        this.items = this.items.filter((id) => id !== card.id);
        this.events.emit('basket:change');
    }
    resetState() {
        this.count = 0;
        this.items = [];
        this.total = 0;
    }
}
