import { View } from '../base/View';
import { ensureAllElements, ensureElement } from '../../utils/utils';
export class OrderView extends View {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._buttons = Array.from(container.querySelectorAll('.order__buttons button') || []);
        this._inputs = ensureAllElements('.form__input', container);
        this._submit = ensureElement('.button[type="submit"]', container);
        this._errors = ensureElement('.form__errors', container);
        this.buttonModifier = 'button_alt-active';
        this._buttons.forEach((button) => {
            button.addEventListener('click', () => {
                this._buttons.forEach((el) => this.toggleClass(el, this.buttonModifier, !button.classList.contains(this.buttonModifier) && button === el));
                if (button.classList.contains(this.buttonModifier)) {
                    const value = button.name;
                    this.onFieldChange(button, 'payment', value);
                }
                else {
                    this.onFieldChange(button, 'payment', '');
                }
            });
        });
        this._inputs.forEach((input) => {
            input.addEventListener('input', (event) => {
                const target = event.target;
                const field = target.name;
                const value = target.value;
                this.onFieldChange(target, field, value);
            });
        });
        this.container.addEventListener('submit', (event) => {
            event.preventDefault();
            const target = event.target;
            const formName = target.name;
            this.events.emit(`order:${formName}-submit`);
        });
        this.events.on('order:reset-view', () => {
            this.valid = false;
            this.errors = '';
            this.container.reset();
            this._buttons.forEach((button) => {
                this.toggleClass(button, this.buttonModifier, false);
            });
        });
    }
    set errors(value) {
        this.setInnerHTML(this._errors, value);
    }
    set valid(value) {
        this._submit.disabled = !value;
        if (value) {
            this.setHidden(this._errors);
        }
        else {
            this.setVisible(this._errors);
        }
    }
    onFieldChange(target, field, value) {
        this.events.emit(`order:field-change`, {
            target,
            field,
            value,
        });
    }
}
